.header {
  align-items: center;
  justify-content: space-between;
}
.header,
.header__left {
  display: flex;
  flex-direction: row;
}
.header__left {
  padding-bottom: 1.25rem;
  padding-left: 2rem;
  padding-top: 1.25rem;
}
.header__left img:first-child {
  padding-right: 0.5rem !important;
}
.header__right {
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding-bottom: 1.25rem;
  padding-right: 4rem;
  padding-top: 1.25rem;
}
.header__right svg:first-child {
  padding-right: 1.9rem !important;
}
.header__right svg:nth-child(2) {
  height: 40px;
  padding-right: 1rem !important;
  width: 40px;
}
.header__right .user {
  box-sizing: border-box;
  height: 2.5rem;
}
.sidebar {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: calc(100vh - 6rem);
  justify-content: space-between;
  position: fixed;
  width: 7rem;
}
.sidebar__bottom ul,
.sidebar__top ul {
  list-style: none;
}
.sidebar__bottom li,
.sidebar__top li {
  align-items: center;
  border-radius: 0.5rem;
  display: flex;
  height: 56px;
  justify-content: center;
  margin-top: 42px;
  width: 56px;
}
.sidebar__bottom li.active,
.sidebar__bottom li:hover,
.sidebar__top li.active,
.sidebar__top li:hover {
  background-color: #5541d7;
}
.sidebar__bottom li.active svg,
.sidebar__bottom li:hover svg,
.sidebar__top li.active svg,
.sidebar__top li:hover svg {
  fill: #fff !important;
}
.sidebar__bottom {
  margin-bottom: 110px;
}
.searchBar {
  align-items: center;
  background-color: #f7f7fc;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: row;
  height: 40px;
  padding: 0.5rem;
}
.searchBar svg {
  padding-right: 0.75rem;
}
.searchBar input {
  background-color: #f7f7fc;
  border: none;
  height: 18px;
  min-width: 120px;
  width: 298px;
}
.categoriesBar {
  display: flex;
  font-size: 1rem;
  overflow-x: scroll;
  padding: 2rem 0 1rem;
  scrollbar-width: none;
}
.categoriesBar::-webkit-scrollbar {
  width: 0;
}
.categoriesBar span {
  padding: 0.5rem;
  text-align: center;
  white-space: nowrap;
  width: 20%;
}
.categoriesBar span.active,
.categoriesBar span:hover {
  border-bottom: 2px solid #5541d7;
}
.menuItems {
  grid-gap: 1rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin: 0.5rem 0;
}
.menuItems .menuItem {
  border: 1px solid #dbd7f4;
  justify-content: center;
  padding: 2rem;
}
.menuItems .menuItem .imageContainer {
  border-radius: 1rem;
  overflow: hidden;
}
.menuItems .menuItem img {
  height: 100%;
  width: 100%;
}
.dashboard {
  background-color: #fff;
  border-radius: 0.5rem;
  padding: 1.5rem;
}
.container {
  background-color: #f7f7fc;
  height: calc(100vh - 10rem);
  margin-left: 7rem;
  padding: 40px 32px;
}
