*,
body {
  margin: 0;
  padding: 0;
}
* {
  font-family: Mulish;
}
#root {
  height: 100vh;
  width: 100vw;
}
a {
  text-decoration: none;
}
h3 {
  font-size: 1.188rem;
}
p {
  color: #92929d;
  font-size: 0.875rem;
}
